<template>
  <div>
    <el-drawer
      title=""
      size="80%"
      :visible.sync="drawer"
      :with-header="false">
        <div style="width:100%;height:100%">
          <i class="el-icon-close" @click="drawer = false" style="font-size:30px;color:#000;position:absolute;top:10px;right:20px;cursor: pointer;"></i>
          <iframe :src="previewHtmlUrl" frameborder="0" style="width:100%;height:100%">
          </iframe>
        </div>
    </el-drawer>

    <el-row :gutter="20">
      <el-col :span="4" :offset="18">
        <el-upload
          class="upload-demo"
          action="http://121.5.66.217:8080/cos/upload"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-success="onSuccessFun"
          :before-remove="beforeRemove"
          multiple
          :limit="30"
          :show-file-list="false"
          :on-exceed="handleExceed"
          :file-list="fileList">
          <el-button size="small" type="primary">点击上传</el-button>
        </el-upload>
      </el-col>

      <el-col :span="20" :offset="2">
        <div class="grid-content bg-purple">
          <div class="list_content" v-for="(item,index) in dataList" :key="index" @click="detailsfun(item)">
            <p class="file_name">{{item.fileName}}</p>
            <img :src="item.img">
          </div>
        </div>
      </el-col>
    </el-row>

    <el-row>
      <el-col :span="4" :offset="18">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          layout="prev, pager, next"
          :page-size="20"
          :total="totalnum">
        </el-pagination>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      dataList: [],
      fileList: [],
      drawer: false,
      previewHtmlUrl: '',
      ataListAll:[],
      totalnum:0,
    }
  },
  mounted() {
    this.getdatList()
  },
  methods: {
    getdatList() {
      this.$axios({
          method:'get',
          url:'http://121.5.66.217:8080/cos/listAll',
          // data:this.qs.stringify({    //这里是发送给后台的数据
          //       userId:this.userId,
          //       token:this.token,
          // })
      }).then((response) =>{  
              //这里使用了ES6的语法
          this.dataListAll = response.data
          this.totalnum = this.dataListAll.length
          this.dataList = this.dataListAll.slice(0,20)     //请求成功返回的数据
      })
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    onSuccessFun() {
      this.$message({
        message: '上传成功',
        type: 'success'
      });
      this.getdatList()
    },
    handleExceed(files, fileList) {
      
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${ file.name }？`);
    },
    detailsfun(val) {
      this.$axios({
          method:'get',
          url:'http://121.5.66.217:8080/doc/getPreviewUrl?fileName=' + val.fileName,
      }).then((response) =>{  
        this.drawer = true
        this.previewHtmlUrl = response.data.previewUrl
        console.log(response.data.previewUrl)
      })
    },
    handleSizeChange(val) {

    },
    handleCurrentChange(val) {
      let beginNum = val*20 - 20
      let endNum = val*20
      this.dataList = this.dataListAll.slice(beginNum,endNum)
    }
  },
}
</script>
<style>
  .grid-content{
    display:flex;
    flex-wrap: wrap;
  }
  .list_content{
    cursor: pointer;
    width: 243px;
    height: 138px;
    border-radius: 6px;
    background:#ccc;
    margin:11px;
    overflow:hidden;
    position:relative;
    border:1px solid #ccc;
  }
  .file_name{
    position: absolute;
    bottom:0;
    left:0;
    width:100%;
    height:30px;
    background:#ccc;
    z-index:9;
    margin:0;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  .list_content img{
    width:100%;
    height:100%;
    position: absolute;
    top:0;
    left:0;
  }
  .list_content img:hover{
    width:110%;
    height:110%;
    top:-5%;
    left:-5%;
  }
</style>


